import { Component, OnInit, DoCheck } from '@angular/core';
import { PlatilloService } from '../../services/platillo.service';
import { Platillo } from '../../platillo';
import { Pedido } from '../../pedido';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit, DoCheck {
  entradas: Platillo[];
  sopas: Platillo[];
  platosFuertes: Platillo[];

  personas: number;
  selectEntrada: Platillo;
  selectSopa: Platillo;
  selectPlatoFuerte: Platillo;
  
  precio: number;

  constructor(private platilloService: PlatilloService) { }

  ngOnInit() {
    
    this.platilloService.getEntradas().subscribe(entradas => {
      this.entradas = entradas;
    });

    this.platilloService.getSopas().subscribe(sopas => {
      this.sopas = sopas; 
    });

    this.platilloService.getPlatosFuertes().subscribe(platosFuertes => {
      this.platosFuertes = platosFuertes;      
    });
  }

  ngDoCheck(){
    if(this.selectEntrada && this.selectSopa && this.selectPlatoFuerte && this.personas){
      var precioEntrada = this.selectEntrada.precio * this.personas;
      var precioSopa = this.selectSopa.precio * this.personas;
      var precioPlatoFuerte = this.selectPlatoFuerte.precio * this.personas;

      this.precio = precioEntrada + precioSopa + precioPlatoFuerte;     
    }
  }

  crearPedido(event){
    event.preventDefault();
    console.log(this.selectEntrada);
    console.log(this.selectSopa);   
    console.log(this.selectPlatoFuerte);       
  }

}



