import { Component, OnInit } from '@angular/core';
import { Platillo } from '../../platillo';
import { PlatilloService } from '../../services/platillo.service';

@Component({
  selector: 'app-entradas',
  templateUrl: './entradas.component.html',
  styleUrls: ['./entradas.component.css']
})
export class EntradasComponent implements OnInit {
  entradas: Platillo[];
  title: string;
  precioNuevo: number;
  
  constructor(private platilloService: PlatilloService) { }

  ngOnInit() {
    this.platilloService.getEntradas().subscribe(entradas => {
      this.entradas = entradas;
    });
  
  }

  agregarEntrada(event){
    event.preventDefault();
    const nuevaEntrada = {
      title: this.title,
      precio: this.precioNuevo
    }

    this.platilloService.addEntrada(nuevaEntrada).subscribe(nuevaEntrada => {
      this.entradas.push(nuevaEntrada);  
      this.title = '';
      this.precioNuevo = null;
    });
  }

  deleteEntrada(id){
    var entradas = this.entradas;
    
    this.platilloService.deletePlatillo(id).subscribe(data => {
      //cuando eliminar algo mongo responde {ok: 1, n: 1} si lo hizo correctamente
      if(data.n == 1){
        for(var i = 0; i < entradas.length; i++){
          if(entradas[i]._id == id){
            entradas.splice(i,1);
          }
        }
      }
    });
  }
  
}



