import { Component, OnInit } from '@angular/core';
import { Platillo } from '../../platillo';
import { PlatilloService } from '../../services/platillo.service'; 

@Component({
  selector: 'app-plato-fuerte',
  templateUrl: './plato-fuerte.component.html',
  styleUrls: ['./plato-fuerte.component.css']
})
export class PlatoFuerteComponent implements OnInit {
  platosFuertes: Platillo[];
  title: string;
  precioNuevo: number;

  constructor(private platilloService: PlatilloService) { }

  ngOnInit() {
    this.platilloService.getPlatosFuertes().subscribe(platosFuertes => {
      this.platosFuertes = platosFuertes
    });
  }

  agregarGuisado(event){
    event.preventDefault();    
    const nuevoplato = {
      title: this.title,
      precio: this.precioNuevo
    }

    this.platilloService.addPlatoFuerte(nuevoplato).subscribe(nuevoplato => {      
      this.platosFuertes.push(nuevoplato);
      this.title = '';
      this.precioNuevo = null;
    });
  }

  deletePlatoFuerte(id){
    var platosFuertes = this.platosFuertes;
    
    this.platilloService.deletePlatillo(id).subscribe(data => {
      //cuando eliminar algo mongo responde {ok: 1, n: 1} si lo hizo correctamente
      if(data.n == 1){
        for(var i = 0; i < platosFuertes.length; i++){
          if(platosFuertes[i]._id == id){
            platosFuertes.splice(i,1);
          }
        }
      }
    });
  }

}



