import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class PlatilloService {
  authToken: any;
  
  constructor(private http: Http) { }

  getEntradas(){
    let headers = new Headers();    
    this.loadToken();
    headers.append('Authorization', this.authToken);

    return this.http.get('http://localhost:3000/admin/entradas', {headers: headers}).map(res => res.json());
  }

  addEntrada(newEntrada){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');

    return this.http.post('http://localhost:3000/admin/crear-entrada', JSON.stringify(newEntrada), {headers: headers}).map(res => res.json());
  }

  getSopas(){
    let headers = new Headers();    
    this.loadToken();
    headers.append('Authorization', this.authToken);

    return this.http.get('http://localhost:3000/admin/sopas', {headers: headers}).map(res => res.json());
  }

  addSopa(newSopa){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');

    return this.http.post('http://localhost:3000/admin/crear-sopa', JSON.stringify(newSopa), {headers: headers}).map(res => res.json());
  }

  getPlatosFuertes(){
    let headers = new Headers();    
    this.loadToken();
    headers.append('Authorization', this.authToken);

    return this.http.get('http://localhost:3000/admin/platos-fuertes', {headers: headers}).map(res => res.json());
  }

  addPlatoFuerte(newPlatoFuerte){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');

    return this.http.post('http://localhost:3000/admin/crear-plato-fuerte', JSON.stringify(newPlatoFuerte), {headers: headers}).map(res => res.json());
  }

  deletePlatillo(id){
    return this.http.delete('http://localhost:3000/admin/platillo/'+id).map(res => res.json());
  }

  //Token para autentificacion.
  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }
}



