import { Component, OnInit } from '@angular/core';
import { Platillo } from '../../platillo';
import { PlatilloService } from '../../services/platillo.service';

@Component({
  selector: 'app-sopas',
  templateUrl: './sopas.component.html',
  styleUrls: ['./sopas.component.css']
})
export class SopasComponent implements OnInit {
  sopas: Platillo[];
  title: string;
  precioNuevo: number;

  constructor(private platilloService: PlatilloService) { }

  ngOnInit() {
    this.platilloService.getSopas().subscribe(sopas => {
      this.sopas = sopas;
    });
  }

  agregarSopa(event){
    event.preventDefault();    
    const nuevaSopa = {
      title: this.title,
      precio: this.precioNuevo
    }

    this.platilloService.addSopa(nuevaSopa).subscribe(nuevaSopa => {
      this.sopas.push(nuevaSopa);
      this.title = '';
      this.precioNuevo = null;
    });
  }

  deleteSopa(id){
    var sopas = this.sopas;
    
    this.platilloService.deletePlatillo(id).subscribe(data => {
      //cuando eliminar algo mongo responde {ok: 1, n: 1} si lo hizo correctamente
      if(data.n == 1){
        for(var i = 0; i < sopas.length; i++){
          if(sopas[i]._id == id){
            sopas.splice(i,1);
          }
        }
      }
    });
  }

}



