import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

//componentes user
import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { HomeComponent } from './components/home/home.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { ProfileComponent } from './components/profile/profile.component';

//componentes admin
import { LoginAdminComponent } from './components/login-admin/login-admin.component';
import { DashboardAdminComponent } from './components/dashboard-admin/dashboard-admin.component';
import { NavbarAdminComponent } from './components/navbar-admin/navbar-admin.component';
import { EntradasComponent } from './components/entradas/entradas.component';
import { PlatoFuerteComponent } from './components/plato-fuerte/plato-fuerte.component';
import { SopasComponent } from './components/sopas/sopas.component';

//servicios admin
import { AuthAdminService } from './services/auth-admin.service';
import { PlatilloService } from './services/platillo.service';

//servicios user
import { ValidateService } from './services/validate.service';
import { AuthService } from './services/auth.service';

//guards user
import { AuthGuard } from './guards/auth.guard';

//guards admin
import { AuthAdminGuard } from './guards/auth-admin.guard';

import { FlashMessagesModule } from 'angular2-flash-messages';

const appRoutes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'login', component: LoginComponent},
  {path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard]},
  {path: 'profile', component: ProfileComponent, canActivate: [AuthGuard]},
  
  //admin
  {path: 'admin/register-qwerty123-admin', component: RegisterComponent},  
  {path: 'admin/login', component: LoginAdminComponent},
  {path: 'admin/dashboard', component: DashboardAdminComponent, canActivate: [AuthAdminGuard]}
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    DashboardComponent,
    ProfileComponent,

    //admin
    LoginAdminComponent,
    DashboardAdminComponent,
    NavbarAdminComponent,
    EntradasComponent,
    PlatoFuerteComponent,
    SopasComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FlashMessagesModule,
  ],
  providers: [
    ValidateService,
    AuthService,
    AuthGuard,

    AuthAdminService,
    AuthAdminGuard,
    PlatilloService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



