import { Component, OnInit } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name: String;
  username: String;
  email: String;
  password: String;

  constructor(
    private validateService: ValidateService,
    private flashMessages: FlashMessagesService,
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  onRegisterSubmit(){
    const user = {
      name: this.name,
      username: this.username,
      email: this.email,
      password: this.password
    }

    //Campos requeridos
    if(!this.validateService.validateRegister(user)){
      this.flashMessages.show("Por favor llena todos los campos", {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    //Validar email
    if(!this.validateService.validateEmail(user.email)){
      this.flashMessages.show("Por favor usa un email valido", {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    //Registro de user en backend
    this.authService.registerUser(user).subscribe(data => {
      if(data.success){
        this.flashMessages.show("Estas registrado y puedes logearte.", {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/login']);     
      }else{
        this.flashMessages.show("Ocurrio un error al registrarse.", {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/register']);     
      }
    });
  }

}



