import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { tokenNotExpired } from 'angular2-jwt';
import 'rxjs/add/operator/map';

@Injectable()
export class AuthAdminService {
  authToken: any;
  user: any;
  admin: any;

  constructor(private http: Http) { }

  registerUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    return this.http.post('http://localhost:3000/admin/register-qwerty123-admin', user, {headers})
      .map(res => res.json());
  }

  authenticateAdmin(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    return this.http.post('http://localhost:3000/admin/authenticate', user, {headers})
      .map(res => res.json());
  }

  getProfile(){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');

    return this.http.get('http://localhost:3000/admin/profile', {headers})
      .map(res => res.json());
  }

  storeAdminData(token, user){
    var valorAdmin = true;

    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    localStorage.setItem('admin', JSON.stringify(valorAdmin));    
    
    this.authToken = token;
    this.user = user;
    this.admin = valorAdmin;    
  }

  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  loggedIn(){
    if(JSON.parse(localStorage.getItem("admin"))){
      return true;      
    }else{
      return false;
    }
  }

  logout(){
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }
}



